.container {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.icon {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
    min-width: 20px;
    min-height: 20px;
    margin-top: 3;
    color: black;
}

.icon:hover {
    color: #444;
}




